<template>
  <div>
    <el-card class="box-card">
      <el-col :span="24">
        <el-row>
          <el-col :span="24">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
              <el-form-item label="设备编号">
                <el-input v-model="formInline.value1" clearable placeholder="根据设备编号查询"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="24" style="margin-top:10px;">
            <el-table :data="tableData" border onselectstart="return false">
              <el-table-column fixed width="150" prop="BusinessNo" label="设备编号" align="center"></el-table-column>
              <el-table-column width="200" prop="lists" label="注册日期" align="center"></el-table-column>
              <el-table-column width="80" label="是否添加设备" align="center">
                <template slot-scope="scope">
                  <i :class="scope.row.uploadState === '成功' ? 'el-icon-success' : 'el-icon-error'"></i>
                </template>
              </el-table-column>
              <el-table-column width="200" prop="uploadDh" label="投放地点" align="center"></el-table-column>
              <el-table-column width="80" prop="uploadDh" label="联系人" align="center"></el-table-column>
              <el-table-column width="130" prop="uploadDh" label="联系方式" align="center"></el-table-column>
              <el-table-column width="200" prop="uploadExplain" label="说明" align="center"></el-table-column>
              <el-table-column fixed="right" label="详情" align="center" v-if="ROLEID == 1">
                <template slot-scope="scope">
                  <el-tooltip content="详情" placement="top">
                    <el-button @click="openVisible(scope.$index, scope.row)" type="primary" icon="el-icon-video-camera-solid"
                      circle></el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              background
              layout="prev, pager, next ,sizes"
              :total="200"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              style="margin-top:10px;"
            ></el-pagination>
          </el-col>
        </el-row>
      </el-col>
    </el-card>
    <el-dialog title="设备详情" width="60%" :visible.sync="dialogVisible">
      <el-form label-width="100px">
        <el-col class="dialog">
          <el-form-item label="设备编号">
            <el-input v-model="SeeClickData.BusinessNo" disabled placeholder="设备编号"></el-input>
          </el-form-item>
          <el-form-item label="注册日期">
            <el-input v-model="SeeClickData.lists" disabled placeholder="注册日期"></el-input>
          </el-form-item>
          <el-form-item label="投放地点">
            <el-input v-model="SeeClickData.uploadDh" placeholder="投放地点"></el-input>
          </el-form-item>
          <el-form-item label="联系人">
            <el-input v-model="SeeClickData.uploadDh" placeholder="联系人"></el-input>
          </el-form-item>
          <el-form-item label="联系方式">
            <el-input v-model="SeeClickData.uploadDh" placeholder="联系方式"></el-input>
          </el-form-item>
          <el-form-item label="说明">
            <el-input v-model="SeeClickData.uploadExplain" placeholder="说明"></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="upAjax">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  const axios = require("axios");
  export default {
    data() {
      return{
        formInline:{
          value1:'',
          value2:''
        },
        tableData:[
          {
            lists:'2020-01-01',
            BusinessNo:'tjj0001',
            uploadDh:'某医院1',
            uploadExplain:'无',
            uploadState:'成功'
          },
          {
            lists:'2020-01-01',
            BusinessNo:'tjj0002',
            uploadDh:'某医院2',
            uploadExplain:'无',
            uploadState:'失败'
          }
        ],
        SeeClickData:'',
        dialogVisible:false,
        ROLEID:''
      }
    },
    methods:{
      onSubmit(){

      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`)
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`)
      },
      openVisible(tab, event) {
        this.SeeClickData = event
        this.dialogVisible = true
        console.log(this.SeeClickData)
      },
      upAjax(){
        console.log('保存')
        this.dialogVisible = false
      }
    },
    mounted(){
      this.ROLEID = sessionStorage.getItem("ROLEID")
    }
  }
</script>
<style scoped>
  .box-card{
    padding: 20px;
  }
  .el-icon-success {
    color: yellowgreen;
    font-size: 20px;
  }

  .el-icon-error {
    color: red;
    font-size: 20px;
  }
  .dialog{
    padding: 20px;
  }
</style>